import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { Gl2StickerUpdateComponent } from './gl2-sticker-update/gl2-sticker-update.component';
import { HomeLayoutComponent } from './home-layout/home-layout.component';
import { LoginLayoutComponent } from './login-layout/login-layout.component';
import { LoginComponent } from './login/login.component';
import { UpdateOpenInventoryComponent } from './update-open-inventory/update-open-inventory.component';
import { VerificationOthersComponent } from './verification-others/verification-others.component';
import { VerificationComponent } from './verification/verification.component';
import { AlertComponent } from './common/alert/alert.component';
import { AuthService } from './services/auth.service';
import { LoadSpinnerComponent } from './common/load-spinner/load-spinner.component';
import { AuditStickerUpdateComponent } from './audit-sticker-update/audit-sticker-update.component';
import { AuctionWeightComponent } from './auction-weight/auction-weight.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { RmPacketVerificationComponent } from './rm-packet-verification/rm-packet-verification.component';
import { RHVERIFICATIONComponent } from './rh-verification/rh-verification.component';
import { RHVERIFICATIONREPORTComponent } from './rh-verification-report/rh-verification-report.component';
import { SampleInspectionComponent } from './sample-inspection/sample-inspection.component';
import { AuditPunchComponent } from './audit-punch/audit-punch.component';
import { NonAuditPaktVerificationComponent } from './non-audit-pakt-verification/non-audit-pakt-verification.component';
import { BlueStickerVerificationComponent } from './blue-sticker-verification/blue-sticker-verification.component';
import { ManualWeighingVerficationComponent } from './manual-weighing-verfication/manual-weighing-verfication.component';
import { ManualWeighingVerficationReportComponent } from './manual-weighing-verfication-report/manual-weighing-verfication-report.component';
import { RiskBasedAuditStickerUpdateComponent } from './risk-based-audit-sticker-update/risk-based-audit-sticker-update.component';
import { RiskBasedTareWeightUpdateComponent } from './risk-based-tare-weight-update/risk-based-tare-weight-update.component';

const routes: Routes = [
 
  {
    path: 'login', component: LoginLayoutComponent,
    children: [
       {path: '', component: LoginComponent} 
      ], 
  },
  { path: 'dashboard', component: HomeLayoutComponent,
    children: [
      { path: '', redirectTo: 'dshbord', pathMatch: 'full' },
      { path: 'dshbord', component: DashboardComponent },
      { path: 'RM-verification', component: RmPacketVerificationComponent },
      { path: 'RH-verification', component: RHVERIFICATIONComponent },
      { path: 'RH-verification-Report', component: RHVERIFICATIONREPORTComponent },
      { path: 'sample-inspection', component: SampleInspectionComponent },
      { path: 'non-audit-packet', component: NonAuditPaktVerificationComponent },
      { path: 'verification', component: VerificationComponent },
      { path: 'verification-others', component: VerificationOthersComponent },
      { path: 'update', component: UpdateOpenInventoryComponent },
      { path: 'auditupdate', component: AuditStickerUpdateComponent },
      { path: 'gl2-sticker', component: Gl2StickerUpdateComponent },
      { path: 'auction', component: AuctionWeightComponent },
      { path: 'auditPunch', component: AuditPunchComponent },
      { path: 'alert', component: AlertComponent },
      { path: 'loader', component: LoadSpinnerComponent },
      { path: 'blue_stik_veri', component: BlueStickerVerificationComponent },
      { path: 'weig_verifi_ah', component: ManualWeighingVerficationComponent },
      { path: 'weig_verifi_ah_report', component: ManualWeighingVerficationReportComponent },
      { path: 'risk_based_audit_sticker_update', component: RiskBasedAuditStickerUpdateComponent },
      { path: 'risk_based_tare_weight_update', component: RiskBasedTareWeightUpdateComponent },
    ],
    canActivate:[AuthService],
  },
  { path: '', redirectTo: 'login' },
  //   { path: '**', redirectTo: 'login',pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes,{useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
