import { Component, OnInit,NgZone } from '@angular/core';
import { FormBuilder, FormGroup, Validators ,NgForm} from '@angular/forms';
import { DataService } from '../services/data.service';
import { AlertService } from '../services/alert.service';
import { Event,NavigationStart,NavigationEnd,Router,NavigationCancel,NavigationError, RouterEvent } from '@angular/router';
import { ElectronService } from 'ngx-electron';
@Component({
  selector: 'app-auction-weight',
  templateUrl: './auction-weight.component.html',
  styleUrls: ['./auction-weight.component.scss']
})
export class AuctionWeightComponent implements OnInit {
  logbrId:any="";
  logbrName:any="";
  logempCode:any="";

  btndis: any = true;
  registerForm !: FormGroup;
  submitted = false;
  msg_supress:any = false;
  responseCode: any = "1";
  responseMsg: any = "";
  weight: any = "0.000";
  dataValue:any=[];
  verifyotherConfirm:any=[];
  constructor( private _electronService:ElectronService, private ngZone:NgZone,private dataService:DataService,private fb: FormBuilder,public router:Router,public alertService:AlertService) { 

  }
  get registerFormControl() {
    return this.registerForm.controls;
  }
  ngOnInit(): void {
    this.logbrId=localStorage.getItem("loggedBrId");
    this.logbrName=localStorage.getItem("loggedBrName");
    this.logempCode=localStorage.getItem("loggedUserId");
    console.log("Branch ID & Name:::"+this.logbrId,this.logbrName)
    this.registerForm = this.fb.group({
      name: ['', [Validators.required]],
      weight: ['',[ Validators.required,Validators.pattern('^[0-9.]+$')]],
    });
    this.registerForm.get('name')!.patchValue(this.logbrId)

  }
  getweight(){
      this.msg_supress = true;
      
      if (this._electronService.isElectronApp) {
        console.log("electron Entry")
        this.ngZone.runOutsideAngular(() => {
          this._electronService.ipcRenderer.send('Weight-Request', 'get - Weight');
          console.log('electron -- Weight-Request')
        });
        this.ngZone.runOutsideAngular(() => {
          console.log('electron -- run')
          this._electronService.ipcRenderer.on('Weight-Reply',(event,arg:any) => {
            console.log('electron -- Weight-Reply'+arg)
            console.log(arg) // prints "Ip Address"
            this.responseCode = arg.split('#')[0];
            console.log(this.responseCode)
            this.responseMsg = arg.split('#')[1];
            this.weight = arg.split('#')[2];
          
            this.ngZone.runTask(() => {
              console.log(this.responseCode)
              if (this.responseCode === "0") {
                console.log("WB Not Connected ... try again")
              }
              else {
                if(this.msg_supress){
                  console.log("WB Succesfully Read")
                  let MachineWgt = parseFloat(this.weight);
                  console.log(MachineWgt)
                  if(Number.isNaN(MachineWgt) || MachineWgt==0.000 || MachineWgt==0)
                  {
                    this.showAlert("Please try Again")
                    this.registerForm.get('weight')!.patchValue("")
                  }
                  else{
                    this.registerForm.get('weight')!.patchValue(MachineWgt)
                  }
                //  this.registerForm.get('newtareWeight')!.patchValue(MachineWgt)
                }
              }
              this.msg_supress = false;
            });
          })
        });
      }
    
  }
  login(){
    this.submitted = true;
    this.dataValue=this.registerForm.value;
    console.log( this.dataValue);
    // if (this.registerForm.valid) {
      if(this.registerForm.value.name ==""){
        this.showAlert("Branch Name is not Null...")
      }
      else if(this.dataValue.weight == "" ){
        this.showAlert("Get weight cannot be blank...!")
      }
      else if(this.dataValue.weight == 0 )
      {
        this.showAlert("Please enter correct reason for 0 Get weight")
      }
      else{
        let data = "brid="+this.logbrId +"&weight=" + this.dataValue.weight +"&logEmpcode=" +this.logempCode +"&status=" +"1";
        this.dataService.auctionGetWeight(data).subscribe((res:any)=>{
          this.verifyotherConfirm=res;
          console.table("Save Data"+JSON.stringify(this.verifyotherConfirm));
          if(this.verifyotherConfirm.status=="111")
          {
            this.showAlert("Successfully Weight Read...!!!");
            this.registerForm.get('weight')!.patchValue('');
          }
          else{
            this.showAlert("ERROR");
            console.log("Service ERROR")
          }
        })
      }
    
    // }
    // else{
    //   this.dataValue=this.registerForm.value;
    //   this.showAlert("Please Enter all feilds correctly.!")
    //   console.log(this.registerForm.value); 
    //   console.log("Invalid")
    // }
  }
  showAlert(msg:any)
{
  this.alertService.confirm('Message',msg)
  .then((confirmed) => console.log('User confirmed:', confirmed))
 .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));

}
}
