import { Component, NgZone, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { ElectronService } from 'ngx-electron';
import { ExcelService } from '../rm-packet-verification/excel.service';
import { AlertService } from '../services/alert.service';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-manual-weighing-verfication-report',
  templateUrl: './manual-weighing-verfication-report.component.html',
  styleUrls: ['./manual-weighing-verfication-report.component.scss']
})
export class ManualWeighingVerficationReportComponent implements OnInit {
  logbrId: any;
  logempCode: any;
  pledgeData: any;
  array: any = [];
  constructor(private dataService: DataService, public router: Router, public alertService: AlertService, private fb: FormBuilder, private _electronService: ElectronService, private ngZone: NgZone,private excelService: ExcelService) { }

  ngOnInit(): void {
    this.logbrId = localStorage.getItem("loggedBrId");
    this.logempCode = localStorage.getItem("loggedUserId");
    let data = "Branchid=" + this.logbrId + "&empCode=" + this.logempCode;
      this.dataService.WeighingVerifiAHReport(data).subscribe((res: any) => {
        this.pledgeData = res.AHverifiedReport;
        
      })      
    this.reportDowload();
  }
  exportAsXLSX(): void {
    this.excelService.exportAsExcelFile(this.array, 'AH Verified Details');
  }
  reportDowload() {
    let data = "Branchid=" + this.logbrId + "&empCode=" + this.logempCode;
    this.dataService.WeighingVerifiAHReport(data).subscribe((res: any) => {
      for (let i of res.AHverifiedReport) {
        this.array.push(i)
      }
    })
  }

}
