import { throwError } from 'rxjs';

export function handleError(error:any){
    if(error.status ===400){
        return throwError(error.error)
    }else if(error.status === 500){
        return throwError(error.error)
    }
    
    else{
        error.status;
        return throwError(error.message || 'Server Error')
    }
}