import { Component, OnInit, NgZone } from '@angular/core';
import { DataService } from '../services/data.service';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertService } from '../services/alert.service';
import { ExcelService } from './excel.service';
import { ElectronService } from 'ngx-electron';
@Component({
  selector: 'app-rm-packet-verification',
  templateUrl: './rm-packet-verification.component.html',
  styleUrls: ['./rm-packet-verification.component.scss']
})
export class RmPacketVerificationComponent implements OnInit {
  registerForm !: FormGroup;
  logbrId: any = "";
  logempCode: any = "";
  deptId:any="";
  postId:any="";
  strickerData: any = [];
  getstickerNo: any = "";
  stickerColor: any = "";
  list: any;
  item: any;
  plgnum: any;
  InventoryDetails: any;
  array: any = [];
  PladgeData: any;
  submitted: boolean = false;
  weighmachineManualstatus: any;
  responseCode: any = "1";
  responseMsg: any = "";
  weight: any = "0.000";
  weighbtn: any = false;
  msg_supress: any = false;
  weighmachinestatus: any;
  negWeightData: any = [];
  irregularity: any = 0;
  verifyotherConfirm: any = [];
  dataValue: any = [];
  tareExcption: any = "";
  constructor(private excelService: ExcelService, private dataService: DataService, public router: Router, public alertService: AlertService, private fb: FormBuilder, private _electronService: ElectronService, private ngZone: NgZone) {
  }
  ngOnInit(): void {
    this.logbrId = localStorage.getItem("loggedBrId");
    this.logempCode = localStorage.getItem("loggedUserId");
    this.deptId = localStorage.getItem("loggedUserDept");
    this.postId = localStorage.getItem("loggedUserPostId");
    if(this.postId =="136" || this.postId =="199" || this.postId== "621"|| this.postId=="248" || this.postId== "309"|| this.postId== "134")
    {  
    }
    else{
      this.showAlert1("You are Not Authorized to View this Page")
    }
    this.getGL2stickerdata();
    this.getInventory();
    this.registerForm = this.fb.group({
      stickerNo: ['', [Validators.required]],
      exitSticker: ['', [Validators.required]],
      invenrynum: ['', [Validators.required]],
      pledgeNo: ['', [Validators.required, Validators.minLength(16), Validators.pattern('^[0-9]+$')]],
      name: ['', [Validators.required]],
      amount: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      actualWeight: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      exittareWeight: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      newtareWeight: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      selfNo: ['', [Validators.required, Validators.pattern('^[a-zA-Z0-9 ]+$')]],
      packetStatus: ['1', [Validators.required]],
      remark: ['', [Validators.required]],
    });
    if (this._electronService.isElectronApp) {
      this.weighbtn = true;
    }
    this.WBStatus();
    this.reportDowload();
  }
  get registerFormControl() {
    return this.registerForm.controls;
  }
  exportAsXLSX(): void {
    this.excelService.exportAsExcelFile(this.array, 'Inventory Details');
  }
  reportDowload() {
    let data = "Branchid=" + this.logbrId + "&Empcode=" + this.logempCode;
    this.dataService.RMPledgeDowload(data).subscribe((res: any) => {
      // this.array.push(res.pledgeData1)
      for (let i of res.pledgeData1) {
        this.array.push(i)
      }
    })
  }
  WBStatus() {
    var data = "branchID=" + this.logbrId;
    this.dataService.wbshow(data).subscribe((result: any) => {
      this.weighmachinestatus = result.weighingBalanceStatus;
      if (this.weighmachinestatus == 0) {
        this.weighbtn = false;
      }
      else if (this.weighmachinestatus == 1) {
        this.weighbtn = true;
      }
      var data1 = "brid=" + this.logbrId;
      this.dataService.wbManualcalibration(data1).subscribe((result: any) => {
        this.weighmachineManualstatus = result.result;
        console.log("ManualWBCalibaration::" + this.weighmachineManualstatus)
        if (this.weighmachinestatus == 0 && this.weighmachineManualstatus == 0) {
          this.showAlert1("Weight Balance Not Connected")
        }
      })
    })
  }
  getGL2stickerdata() {
    let data = "brid=" + this.logbrId + "&Empcode=" + this.logempCode;
    this.dataService.updateShowSticket(data).subscribe((res: any) => {
      this.strickerData = res;
      console.log("GL2" + JSON.stringify(res))
      if (this.strickerData.status == 111) {
        this.getstickerNo = this.strickerData.result.split('~')[1];
        this.stickerColor = this.strickerData.result.split('~')[2];
        this.registerForm.get('stickerNo')!.patchValue(this.getstickerNo)
        console.log("color::" + this.stickerColor)
      }
      else {
        this.showAlert("Please Receive Sticker in your Branch..!")
      }
    })
  }
  getInventory() {
    // let data = "brid="+this.logbrId;
    // this.dataService.getInventoryNumber(data).subscribe((res)=>{
    //   this.list=res;
    //   this.item=this.list.Itemlist1;
    //   // console.log("List Invetory Iteam",JSON.stringify(this.list));
    // })
    let data2 = "Branchid=" + this.logbrId + "&empCode=" + this.logempCode;
    this.dataService.RMPledgeLoad(data2).subscribe((res: any) => {
      this.strickerData = res.pledgeData.value;
      this.item = res.pledgeData;
    })
  }
  onChange(newObj: any) {
    console.log("PLG NO:" + newObj);
    this.plgnum = newObj
    let data = "pledgeno=" + this.plgnum + "&Brid=" + this.logbrId;
    let data3 = "pledgeNo=" + this.plgnum + "&Branchid=" + this.logbrId;
    this.dataService.RMPledgeSelect(data3).subscribe((res: any) => {
      this.PladgeData = res.selectPlNo
      let sticker1 = this.PladgeData[0].stickerNo;
      console.log("DATA:::" + JSON.stringify(this.PladgeData))
      this.registerForm.get('exitSticker')!.patchValue(this.PladgeData[0].stickerNo)
      this.registerForm.get('name')!.patchValue(this.PladgeData[1].customerName)
      this.registerForm.get('pledgeNo')!.patchValue(this.PladgeData[1].pledgeNo)
      this.registerForm.get('amount')!.patchValue(this.PladgeData[1].pledgeValue)
      this.registerForm.get('actualWeight')!.patchValue(this.PladgeData[1].actualWeight)
      this.registerForm.get('exittareWeight')!.patchValue(this.PladgeData[1].tareWeight)
      this.registerForm.get('invenrynum')!.patchValue(this.PladgeData[2].inventoryNo)
      this.tareExcption = this.PladgeData[1].tareWeightExe;
    })
  }

  GetWeight() {
    this.msg_supress = true;

    if (this._electronService.isElectronApp) {
      console.log("electron Entry")
      this.ngZone.runOutsideAngular(() => {
        this._electronService.ipcRenderer.send('Weight-Request', 'get - Weight');
        console.log('electron -- Weight-Request')
      });
      this.ngZone.runOutsideAngular(() => {
        console.log('electron -- run')
        this._electronService.ipcRenderer.on('Weight-Reply', (event, arg: any) => {
          console.log('electron -- Weight-Reply' + arg)
          console.log(arg) // prints "Ip Address"
          this.responseCode = arg.split('#')[0];
          console.log(this.responseCode)
          this.responseMsg = arg.split('#')[1];
          this.weight = arg.split('#')[2];

          this.ngZone.runTask(() => {
            console.log(this.responseCode)
            if (this.responseCode === "0") {
              console.log("WB Not Connected ... try again")
            }
            else {
              if (this.msg_supress) {
                console.log("WB Succesfully Read")
                let MachineWgt = parseFloat(this.weight);
                console.log(MachineWgt)
                if (Number.isNaN(MachineWgt) || MachineWgt == 0.000 || MachineWgt == 0) {
                  this.showAlert("Please try Again")
                  this.registerForm.get('newtareWeight')!.patchValue("")
                }
                else {
                  this.registerForm.get('newtareWeight')!.patchValue(MachineWgt)
                }
                //  this.registerForm.get('newtareWeight')!.patchValue(MachineWgt)
              }
            }
            this.msg_supress = false;
          });
        })
      });
    }
  }


  onSubmit() {
    this.submitted = true;
    this.dataValue = this.registerForm.value;
    console.log(this.dataValue);
    // if (this.registerForm.valid) {
    var differnce = (this.registerForm.value.newtareWeight - this.registerForm.value.exittareWeight).toFixed(3);
    console.log('-->'+differnce);
    var difference = parseFloat((this.registerForm.value.newtareWeight - this.registerForm.value.exittareWeight).toFixed(3));
    // if (this.registerForm.value.stickerNo == "") {
    //   this.showAlert("Sticker Number not read in Mobile App...!")
    // }
     if (this.dataValue.newtareWeight == "") {
      this.showAlert("Tare weight cannot be blank...!")
    }
    else if (this.dataValue.newtareWeight == 0) {
      this.showAlert("Please enter correct reason for 0 tare weight")
    }
    else if (this.registerForm.controls.newtareWeight.errors?.pattern) {
      this.showAlert("Please enter Tare Weight correctly..!..!")
    }
    // else if (differnce >= "0.2") {
    //   this.showAlert("High Difference,Please Recheck!!!")
    // }
    else if (Math.abs(difference) >= 1.01) {
      this.showAlert2("Tare weight difference is more than 1 gram; do you want to confirm?")
    }
    else if (differnce > "100") {
      this.showAlert("High Difference,Please Recheck!!!")
    }
    else if (this.registerForm.value.remark == "") {
      this.showAlert("Please Enter the Remark!!!")
    }
    else if (this.registerForm.controls.selfNo.errors?.pattern) {
      this.showAlert("Please enter Shelf Number correctly..!")
    }
    else if (this.dataValue.selfNo == "") {
      this.showAlert("Shelf Number cannot be blank...!")
    }
    // else if(this.dataValue.newtareWeight > 500){
    //   this.showAlert("Tare weight is grater than 500")
    // }
    else if (this.tareExcption == "0") {
      this.neglanceCheck();
    }
    else {
       this.irregularity = 0;
      console.log("Irregularty IS::" + this.irregularity)
      let data = "PledgeNo=" + this.dataValue.pledgeNo + "&brid=" + this.logbrId + "&NewTarewt=" + this.dataValue.newtareWeight + "&Empcode=" + this.logempCode + "&Shelfno=" + this.dataValue.selfNo + "&wbstatus=" + "0" + "&Stickerno=" + this.dataValue.exitSticker + "&irr_flag=" + this.irregularity + "&Existtrwt=" + this.dataValue.exittareWeight + "&packet_status=" + this.dataValue.packetStatus;
      this.dataService.RMPledgeConfirm(data).subscribe((res: any) => {
        this.verifyotherConfirm = res;
        console.table("Confirm Data" + JSON.stringify(this.verifyotherConfirm));
        if (this.verifyotherConfirm.status == "111") {
          this.showAlert(this.verifyotherConfirm.result);
          this.registerForm.get('stickerNo')!.patchValue('');
          this.registerForm.get('pledgeNo')!.patchValue('');
          this.registerForm.get('name')!.patchValue('');
          this.registerForm.get('amount')!.patchValue('');
          this.registerForm.get('actualWeight')!.patchValue('');
          this.registerForm.get('exittareWeight')!.patchValue('');
          this.registerForm.get('newtareWeight')!.patchValue('');
          this.registerForm.get('selfNo')!.patchValue('');
          this.registerForm.get('invenrynum')!.patchValue('');
          this.registerForm.get('exitSticker')!.patchValue('');
          this.registerForm.get('remark')!.patchValue('');
          //this.irregularity = "";
          this.array = [];
          this.reportDowload();
          this.getInventory();
        }
        else {
          this.showAlert(this.verifyotherConfirm.result);
          console.log("Service ERROR")
        }
      })
    }
  }
  neglanceCheck() {
    console.log("Get Weight")
    let data = "brid=" + this.logbrId;
    this.dataService.stickerNeglance(data).subscribe((res: any) => {
      this.negWeightData = res;
      console.log("Neglance Data is::" + JSON.stringify(this.negWeightData))
      if (this.negWeightData.status == "222" && this.negWeightData.result != "") {
        this.showAlert(this.negWeightData.result)
      }
      else {
        let negligibleValue = this.negWeightData.result;
        console.log("Negligibale Value is::" + negligibleValue)
        var firstDiff = (this.registerForm.value.newtareWeight - this.registerForm.value.exittareWeight).toFixed(3);
        console.log("First Difference value::" + firstDiff)
        var secondDiff = (this.registerForm.value.exittareWeight - this.registerForm.value.newtareWeight).toFixed(3);
        console.log("First Difference value::" + secondDiff)
        if (firstDiff > negligibleValue || secondDiff > negligibleValue) {
          this.irregularity = 1;
          console.log("Irregularty IS::" + this.irregularity)
          let data = "PledgeNo=" + this.dataValue.pledgeNo + "&brid=" + this.logbrId + "&NewTarewt=" + this.dataValue.newtareWeight + "&Empcode=" + this.logempCode + "&Shelfno=" + this.dataValue.selfNo + "&wbstatus=" + "0" + "&Stickerno=" + this.dataValue.exitSticker + "&irr_flag=" + this.irregularity + "&Existtrwt=" + this.dataValue.exittareWeight + "&packet_status=" + this.dataValue.packetStatus;
          this.dataService.RMPledgeConfirm(data).subscribe((res: any) => {
            this.verifyotherConfirm = res;
            console.table("Confirm Data" + JSON.stringify(this.verifyotherConfirm));
            if (this.verifyotherConfirm.status == "111") {
              this.showAlert(this.verifyotherConfirm.result);
              this.registerForm.get('stickerNo')!.patchValue('');
              this.registerForm.get('pledgeNo')!.patchValue('');
              this.registerForm.get('name')!.patchValue('');
              this.registerForm.get('amount')!.patchValue('');
              this.registerForm.get('actualWeight')!.patchValue('');
              this.registerForm.get('exittareWeight')!.patchValue('');
              this.registerForm.get('newtareWeight')!.patchValue('');
              this.registerForm.get('selfNo')!.patchValue('');
              this.registerForm.get('invenrynum')!.patchValue('');
              this.registerForm.get('exitSticker')!.patchValue('');
              this.registerForm.get('remark')!.patchValue('');
              this.irregularity = ""; 
              this.array = [];           
              this.reportDowload();
              this.getInventory();
            }
            else {
              this.showAlert(this.verifyotherConfirm.result);
              console.log("Service ERROR")
            }
          })
        }
      }
    })
  }
  showAlert(msg: any) {
    this.alertService.confirm('Message', msg)
      .then((confirmed) => console.log('User confirmed:', confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));
  }
  showAlert1(msg: any) {
    this.alertService.confirm('Message', msg)
      .then((confirmed) => console.log('User confirmed:', confirmed))
      .then((confirmed) => (this.router.navigate(['/dashboard/dshbord']), confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));
  }
  showAlert2(msg: any) {
    this.alertService.confirm('Message', msg)
      .then((confirmed) => (this.showAlert("Tare weight difference is more than 1 gram, Sticker updation is compulsory, need to open the packet and verify the gold."),confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));
  }
}

