import { Component, OnInit, NgZone } from '@angular/core';
import { DataService } from '../services/data.service';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { AlertService } from '../services/alert.service';
import { ElectronService } from 'ngx-electron';
@Component({
  selector: 'app-sample-inspection',
  templateUrl: './sample-inspection.component.html',
  styleUrls: ['./sample-inspection.component.scss']
})
export class SampleInspectionComponent implements OnInit {
  registerForm !: FormGroup;
  logbrId: any = "";
  deptId:any="";
  postId:any="";
  logempCode: any = "";
  strickerData: any = [];
  Stickerdata: any = [];
  updateData: any = [];
  irregularityType: any = 0;
  tareExp: any;
  barcodeStatus: any = "";
  getstickerNo: any = "";
  stickerColor: any = "";
  submitted = false;
  showRadioBtns: Boolean = false;
  weighbtn: any = false;
  msg_supress: any = false;
  responseCode: any = "1";
  responseMsg: any = "";
  weight: any = "0.000";
  weighmachinestatus: any;
  weighmachineManualstatus: any;
  branchnam: any;
  invntrynum: any;
  list: any;
  item: any;
  plgnum: any;
  InventoryDetails: any;
  inspectID: any="";
  nispectData: any;
  newStickerdata: any = [];
  dataValue: any = [];
  updateConfirmdata: any = [];
  negWeightData: any = [];
  irr_data: any;
  StickerCheck: any;
  Irregu1: any;
  Irregu2: any;
  access: any;
  constructor(private _electronService: ElectronService, private ngZone: NgZone, private dataService: DataService, public router: Router, public alertService: AlertService, private fb: FormBuilder) { }

  ngOnInit(): void {
    this.logbrId = localStorage.getItem("loggedBrId");
    this.logempCode = localStorage.getItem("loggedUserId");
    this.deptId = localStorage.getItem("loggedUserDept");
    this.postId = localStorage.getItem("loggedUserPostId");
    if(((this.deptId=="4" || this.deptId=="211")  && this.postId=="621") || (this.deptId=="4" && (this.postId=="248" || this.postId== "309")))
    {
      var data = "brid=" + this.logbrId + "&Empcode=" + this.logempCode;;
      this.dataService.sampleInsAccess(data).subscribe((result: any) => {
       this.access=result
      })
    }
    else{
      this.showAlert2("You are Not Authorized to view this Page");
    }
    console.log("Branch ID:::" + this.logbrId, this.logempCode)
    this.registerForm = this.fb.group({
     
      complaintCheck: [false],
      complaintItem: [''],
      exitstikerNo: ['', [Validators.required, Validators.pattern('^[0-9A-Za-z]+$')]],
      pledgeNo: ['', [Validators.required, Validators.minLength(16), Validators.pattern('^[0-9]+$')]],
      name: ['', [Validators.required]],
      amount: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      pledgeDate: ['', [Validators.required]],
      grassWeight: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      exittareWeight: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      newtareWeight: ['', [Validators.required, Validators.pattern('^[0-9.]+$')]],
      packetStatus: ['1', [Validators.required]],
      goldStatus: ['0', [Validators.required]],
      remark: ['', [Validators.required]],
      newSticker: ['', [Validators.required, Validators.pattern('^[0-9A-Za-z]+$')]],
      vrifid: ['', [Validators.required]],
      pndng: ['', [Validators.required]],
      rsk_catagry: ['', [Validators.required]],
      rsk_reson: ['', [Validators.required]],
      selectgoldpurty: ['0']
    });
    this.WBStatus();
    this.getGL2stickerdata();
    this.getInventory();
    // this.registerForm.get('selectgoldpurty')!.patchValue('0')
  }
  WBStatus() {
    var data = "branchID=" + this.logbrId;
    this.dataService.wbshow(data).subscribe((result: any) => {
      this.weighmachinestatus = result.weighingBalanceStatus;

      if (this.weighmachinestatus == 0) {
        this.weighbtn = false;
      }
      else if (this.weighmachinestatus == 1) {
        this.weighbtn = true;
      }
    })
    var data1 = "brid=" + this.logbrId;
    this.dataService.wbManualcalibration(data1).subscribe((result: any) => {
      this.weighmachineManualstatus = result.result;
      console.log("ManualWBCalibaration::" + this.weighmachineManualstatus)
    })
  }
  getGL2stickerdata() {
    let data = "brid=" + this.logbrId + "&Empcode=" + this.logempCode;
    this.dataService.updateShowSticket(data).subscribe((res: any) => {
      this.strickerData = res;
      console.log("GL2" + JSON.stringify(res))
      if (this.strickerData.status == 111) {
        this.getstickerNo = this.strickerData.result.split('~')[1];
        this.stickerColor = this.strickerData.result.split('~')[2];
        console.log("color::" + this.stickerColor)
      }
      else {
        this.showAlert("Please Receive Sticker in your Branch..!")
      }
    })
  }

  getInventory() {
    // let data = "brid=" + this.logbrId;
    let data = "brid=" + this.logbrId;
    this.dataService.sampleInsPldgeload(data).subscribe((res) => {
      this.list = res;
      this.item = this.list.RHselectPlNo;
      // console.log("List Invetory Iteam",JSON.stringify(this.list));s
    })
    let data1 = "brid=" + this.logbrId+ "&Empcode=" + this.logbrId;
    this.dataService.sampleInsPendingCount(data1).subscribe((res) => {
      this.list = res;
      console.log("Pndh"+this.list);      
      this.registerForm.get('vrifid')!.patchValue(this.list.result1)
      this.registerForm.get('pndng')!.patchValue(this.list.result2)
      // console.log("List Invetory Iteam",JSON.stringify(this.list));s
    })
  }
  onChange(newObj: any) {
    console.log("PLG NO:" + newObj);
    this.plgnum = newObj
    let data = "pledgeno=" + this.plgnum + "&Brid=" + this.logbrId;
    if (this.list.status == "111") {
      this.dataService.sampleInsPldgeSelect(data).subscribe((res) => {
        console.log("InventoryDetails" + JSON.stringify(res));
        this.InventoryDetails = res
        this.registerForm.get('pledgeNo')!.patchValue(this.InventoryDetails.plno)
        this.registerForm.get('name')!.patchValue(this.InventoryDetails.name)
        this.registerForm.get('amount')!.patchValue(this.InventoryDetails.amount)
        this.registerForm.get('pledgeDate')!.patchValue(this.InventoryDetails.pldate)
        this.registerForm.get('grassWeight')!.patchValue(this.InventoryDetails.grswgt)
        this.registerForm.get('exittareWeight')!.patchValue(this.InventoryDetails.tarewgt)
        this.registerForm.get('exitstikerNo')!.patchValue(this.InventoryDetails.ex_sticker)
        this.registerForm.get('rsk_catagry')!.patchValue(this.InventoryDetails.riskcategory)
        this.registerForm.get('rsk_reson')!.patchValue(this.InventoryDetails.riskreason)
        let data = "brid=" + this.logbrId+ "&StickerNo=" + this.InventoryDetails.ex_sticker;
        this.dataService.sampleInsPldgeStickerCheck(data).subscribe((res) => {
          this.StickerCheck = res;
          console.log("StickerCheck"+JSON.stringify(this.StickerCheck)); 
          this.tareExp = this.StickerCheck.TrewtExcptn;
          this.irregularityType = this.StickerCheck.TrewtExcptn
          if (this.StickerCheck.status == "333" || this.StickerCheck.status == "444" || this.StickerCheck.status == "555") {
            this.showAlert(this.StickerCheck.Irregularity)
          }     
        })      
      })
    }
    let data1 = "brid=" + this.logbrId;
    this.dataService.get_inspect_id(data1).subscribe((res) => {
      this.nispectData = res;
      this.inspectID = this.nispectData.inspectid;
      console.log("Inspect ID::" + this.inspectID);
    })   
    let data2 = "pledgeNo=" + this.plgnum+ "&branchID=" + this.logbrId;
    this.dataService.sampleInsIrregu1(data2).subscribe((res) => {
      this.Irregu1 = res;
      console.log("Irregu1"+JSON.stringify(this.Irregu1));      
    })
    let data3 = "pledgeNo=" + this.plgnum;
    this.dataService.sampleInsIrregu2(data3).subscribe((res) => {
      this.Irregu2 = res;
      console.log("Irregu2"+JSON.stringify(this.Irregu2));      
    }) 
  }
  get registerFormControl() {
    return this.registerForm.controls;
  }

  
  getWeight() {
    this.msg_supress = true;
    if (this._electronService.isElectronApp) {
      console.log("Enter")
      this.ngZone.runOutsideAngular(() => {
        this._electronService.ipcRenderer.send('Weight-Request', 'get - Weight');
        console.log("Enter get - Weight")
      });
      this.ngZone.runOutsideAngular(() => {
        this._electronService.ipcRenderer.on('Weight-Reply', (event, arg) => {
          console.log("Enter --Weight-Reply")
          console.log(arg) // prints "Ip Address"
          this.responseCode = arg.split('#')[0];
          console.log(this.responseCode)
          this.responseMsg = arg.split('#')[1];
          this.weight = arg.split('#')[2];
          console.log(this.weight)
          this.ngZone.runTask(() => {
            if (this.responseCode === "0") {
              // this.showAlert("Please Try Again")
              console.log("WB Not Connected ... try again")
            }
            else {
              if (this.msg_supress) {
                console.log("WB Succesfully Read")
                let MachineWgt = parseFloat(this.weight);
                console.log("Weight is:::" + MachineWgt)
                if (Number.isNaN(MachineWgt) || MachineWgt == 0.000 || MachineWgt == 0) {
                  this.showAlert("Please try Again")
                  this.registerForm.get('newtareWeight')!.patchValue("")
                }
                // else if( this.weight=='0.000'){
                //   this.showAlert("Please try Again")
                //   this.registerForm.get('newtareWeight')!.patchValue("")
                // }
                else {
                  this.registerForm.get('newtareWeight')!.patchValue(MachineWgt)
                }
                
              }
            }
            this.msg_supress = false;
          });
        })
      });
    }
  }
  checkbox(ev: any) {
    console.log(ev.target.checked)
    let radioEnable = ev.target.checked;
    if (this.showRadioBtns = radioEnable) {
      this.showRadioBtns = true;
      console.log(this.showRadioBtns)
      this.registerForm.get('complaintItem')!.patchValue('0')
    }
    else if (this.showRadioBtns = radioEnable) {
      this.showRadioBtns = false;
      console.log(this.showRadioBtns)
      this.registerForm.get('complaintItem')!.patchValue('')
    }
  }
  
  
  getnewSticker() {
    let data = "Branchid=" + this.logbrId + "&userid=" + this.logempCode;
    this.dataService.gl2newSticker(data).subscribe((res) => {
      this.newStickerdata = res;
      if (this.newStickerdata.status1 == "111" && this.newStickerdata.stickerNo == this.getstickerNo) {
        this.registerForm.get('newSticker')!.patchValue(this.newStickerdata.stickerNo)
        this.barcodeStatus = 2;
      }
      else {
        this.showAlert("Please Read the Correct sticker Number in Mobile App.")
      }
    })
  }


 
  onSubmit() {
    this.submitted = true;

    console.log("Complient :" + this.showRadioBtns)
    this.dataValue = this.registerForm.value;
    if (this.showRadioBtns == false && this.registerForm.value.exitstikerNo == "") {
      this.showAlert("Sticker Number not read in Mobile App...!")
    }
    else if (this.showRadioBtns == false && this.registerForm.value.pledgeNo == "") {
      this.showAlert("Pledge number cannot be blank..")
    }
    else if (this.showRadioBtns == false && this.registerForm.value.newtareWeight == "") {
      this.showAlert("Tare weight Can't be blank ")
    }
    else if (this.showRadioBtns == false && this.registerForm.value.newtareWeight == 0) {
      this.showAlert("Please enter correct reason for 0 tare weight")
    }
    // else if(this.showRadioBtns==false && this.registerForm.value.newtareWeight =="" && this.barcodeStatus !=2){
    //   else if(this.showRadioBtns==false && this.registerForm.value.newtareWeight =="" && this.barcodeStatus !=2){

    //   this.showAlert("New Sticker Number not read in Mobile App..")
    // }
    else if (this.showRadioBtns == false && this.registerForm.value.newSticker=="") {
      this.showAlert("Please read New sticker Number in Mobile App")
    }
    else if (this.showRadioBtns == false && this.getstickerNo != this.registerForm.value.newSticker) {
      this.showAlert("Sticker number doesn't match old sticker number....Please update Current sticker Number in Mobile App")
      console.log(this.getstickerNo)
      this.registerForm.get('newSticker')!.patchValue('');
    }
    else if (this.registerForm.value.remark == "") {
      this.showAlert("Please Enter the Remark...!")
    }
    // else if(this.registerForm.value.newtareWeight > 500){
    //   this.showAlert("Tare weight is grater than 500")
    // }
    else if (this.tareExp == "0") {
      this.neglanceCheck();
    }
    else {
      
      if (this.dataValue.complaintCheck == false) {
        this.registerForm.value.complaintCheck = "1";
        this.registerForm.value.complaintItem = '';
      }
      else {
        this.registerForm.value.complaintCheck = "2";
      }
      this.barcodeStatus = 2;
      let data = "PledgeNo=" + this.dataValue.pledgeNo + "&Tarewt=" + this.dataValue.newtareWeight + "&Empcode=" + this.logempCode + "&Remark=" + this.dataValue.remark + "&brid=" + this.logbrId + "&irr_flag=" + this.dataValue.goldStatus + "&Packetsts=" + this.dataValue.packetStatus + "&wbstatus=" + "0" + "&Barcodests=" + this.barcodeStatus + "&Compli=" + this.dataValue.complaintCheck + "&CompItem=" + this.dataValue.complaintItem + "&Stickerno=" + this.getstickerNo + "&inspect_id=" + this.inspectID;
      console.log("Reqst DTA::");
      console.log(this.registerForm.value);
      this.dataService.sampleInsPldgeConfirm(data).subscribe((res: any) => {
        this.updateConfirmdata = res;
        console.log(this.updateConfirmdata);
        if (this.updateConfirmdata.status == "111") {
          this.showAlert(this.updateConfirmdata.result)
          this.showRadioBtns = false;
          console.log(this.showRadioBtns)
          this.registerForm.value.complaintCheck = "1";
          this.registerForm.get('complaintCheck')!.patchValue('');
          this.registerForm.get('complaintItem')!.patchValue('');
          this.registerForm.get('pledgeNo')!.patchValue('');
          this.registerForm.get('name')!.patchValue('');
          this.registerForm.get('amount')!.patchValue('');
          this.registerForm.get('pledgeDate')!.patchValue('');
          this.registerForm.get('grassWeight')!.patchValue('');
          this.registerForm.get('exittareWeight')!.patchValue('');
          this.registerForm.get('newtareWeight')!.patchValue('');
          this.registerForm.get('remark')!.patchValue('');
          this.registerForm.get('exitstikerNo')!.patchValue('');
          this.registerForm.get('newSticker')!.patchValue('');
          this.registerForm.get('rsk_catagry')!.patchValue('');
          this.registerForm.get('rsk_reson')!.patchValue('');
          // this.registerForm.get('invntyNumbr')!.patchValue('');
          this.registerForm.get('vrifid')!.patchValue('');
          this.registerForm.get('pndng')!.patchValue('');
          this.getstickerNo = "";
          this.getGL2stickerdata();
          this.getInventory();
        }
      })
    }
  }
  neglanceCheck() {
    console.log("Get Weight")
    let data = "brid=" + this.logbrId;
    this.dataService.stickerNeglance(data).subscribe((res: any) => {
      this.negWeightData = res;
      console.log("Neglance Data is::" + JSON.stringify(this.negWeightData))
      if (this.negWeightData.status == "222" && this.negWeightData.result != "") {
        this.showAlert(this.negWeightData.result)
      }
      else {
        let negligibleValue = this.negWeightData.result;
        console.log("Negligibale Value is::" + negligibleValue)
        var firstDiff = (this.registerForm.value.newtareWeight - this.registerForm.value.exittareWeight).toFixed(3);
        console.log("First Difference value::" + firstDiff)
        var secondDiff = (this.registerForm.value.exittareWeight - this.registerForm.value.newtareWeight).toFixed(3);
        console.log("First Difference value::" + secondDiff)
        if (firstDiff > negligibleValue || secondDiff > negligibleValue) {
          this.irregularityType = 1;
          if (this.dataValue.complaintCheck == false) {
            this.registerForm.value.complaintCheck = "1";
            this.registerForm.value.complaintItem = '';
          }
          else {
            this.registerForm.value.complaintCheck = "2";
          }
          this.barcodeStatus = 2;
          let data = "PledgeNo=" + this.dataValue.pledgeNo + "&Tarewt=" + this.dataValue.newtareWeight + "&Empcode=" + this.logempCode + "&Remark=" + this.dataValue.remark + "&brid=" + this.logbrId + "&irr_flag=" + this.dataValue.goldStatus + "&Packetsts=" + this.dataValue.packetStatus + "&wbstatus=" + "0" + "&Barcodests=" + this.barcodeStatus + "&Compli=" + this.dataValue.complaintCheck + "&CompItem=" + this.dataValue.complaintItem + "&Stickerno=" + this.getstickerNo + "&inspect_id=" + this.inspectID;

         // let data = "PledgeNo=" + this.dataValue.pledgeNo + "&Tarewt=" + this.dataValue.newtareWeight + "&brid=" + this.logbrId + "&Empcode=" + this.logempCode + "&Remark=" + this.dataValue.remark + "&Packetsts=" + this.dataValue.packetStatus + "&wbstatus=" + "0" + "&Barcodests=" + this.barcodeStatus + "&Compli=" + this.dataValue.complaintCheck + "&Stickerno=" + this.getstickerNo + "&CompItem=" + this.dataValue.complaintItem + "&irr_flag=" + this.irregularityType + "&Exetrwt=" + this.dataValue.exittareWeight + "&inspect_id=" + this.inspectID;
          console.log(this.registerForm.value);
          this.dataService.sampleInsPldgeConfirm(data).subscribe((res: any) => {
            this.updateConfirmdata = res;
            console.log(this.updateConfirmdata);
            if (this.updateConfirmdata.status == "111") {
              this.showAlert(this.updateConfirmdata.result)
              this.showRadioBtns = false;
              console.log(this.showRadioBtns)
              this.registerForm.value.complaintCheck = "1";
              this.registerForm.get('complaintCheck')!.patchValue('');
              this.registerForm.get('complaintItem')!.patchValue('');
              this.registerForm.get('pledgeNo')!.patchValue('');
              this.registerForm.get('name')!.patchValue('');
              this.registerForm.get('amount')!.patchValue('');
              this.registerForm.get('pledgeDate')!.patchValue('');
              this.registerForm.get('grassWeight')!.patchValue('');
              this.registerForm.get('exittareWeight')!.patchValue('');
              this.registerForm.get('newtareWeight')!.patchValue('');
              this.registerForm.get('remark')!.patchValue('');
              this.registerForm.get('newSticker')!.patchValue('');
              this.registerForm.get('exitstikerNo')!.patchValue('');
              this.registerForm.get('rsk_catagry')!.patchValue('');
              this.registerForm.get('rsk_reson')!.patchValue('');
              this.registerForm.get('vrifid')!.patchValue('');
              this.registerForm.get('pndng')!.patchValue('');
              this.getstickerNo = "";
              this.getGL2stickerdata();
              this.getInventory();
            }
          })
        }
      }
    })
  }
  irreglrty(evnt: any) {
    // alert(evnt.target.checked)  
    var data = "pledgeno=" + this.plgnum + "&Brid=" + this.logbrId;
    this.dataService.irregularityCheck(data).subscribe((res: any) => {
      this.irr_data = res
      console.log("Irregularity Check::" + JSON.stringify(res))
      if (res.status == "111") {
        this.showAlert2(res.result)
      }
    })
  }
  showAlert(msg: any) {
    this.alertService.confirm('Message', msg)
      .then((confirmed) => console.log('User confirmed:', confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));

  }
  showAlert2(msg: any) {
    this.alertService.confirm('Message', msg)
      .then((confirmed) => console.log('User confirmed:', confirmed))
      .then((confirmed) => (this.router.navigate(['/dashboard/dshbord']), confirmed))
      .catch(() => console.log('User dismissed the dialog (e.g., by using ESC, clicking the cross icon, or clicking outside the dialog)'));
  }
}